@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@100;200;300;400;500;600;700&family=Montserrat:wght@100..900&display=swap";
h1 {
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

.subscription__title, .gallery__title, .current-events__title {
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
}

@media (width >= 768px) {
  .subscription__title, .gallery__title, .current-events__title {
    font-size: 48px;
    line-height: 48px;
  }
}

.footer__copyright, .footer__design-auther {
  text-align: center;
  font-size: 12px;
  font-weight: 300;
  line-height: 17px;
}

@font-face {
  font-family: "IBM Plex Sans", Montserrat, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scrollbar-width: none;
  overflow-x: hidden;
}

html:has(.menu:target) {
  overflow: hidden;
}

body {
  margin: 0;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  line-height: 24px;
}

h1, h2, h3, h4 {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.header {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  margin-inline: auto;
  gap: 0 20px;
  margin-bottom: 70px;
  padding: 0;
  display: grid;
  position: relative;
}

@media (width >= 768px) {
  .header {
    --columns: 6;
    column-gap: 30px;
    padding-inline-start: 34px;
  }
}

@media (width >= 1280px) {
  .header {
    --columns: 12;
    padding-inline-start: 55px;
  }
}

@media (width >= 768px) {
  .header {
    margin-bottom: 88px;
  }
}

@media (width >= 1280px) {
  .header {
    margin-bottom: 400px;
  }
}

.header__bg-field {
  background-image: url("header-bg.38260647.png");
  background-size: cover;
  grid-column: 1 / -1;
  width: 100%;
  height: 94vw;
}

@media (width >= 768px) {
  .header__bg-field {
    order: 2;
    grid-column: 4 / -1;
    width: 100%;
    height: 50vw;
  }
}

@media (width >= 1280px) {
  .header__bg-field {
    grid-column: 6 / -1;
    width: 58vw;
    height: 51vw;
    position: absolute;
  }
}

.header__menu {
  z-index: 6;
  cursor: pointer;
  background-image: url("menu-icon.ac307ddf.png");
  background-position: center;
  width: 24px;
  height: 24px;
  transition: transform .3s;
  position: absolute;
  top: 27px;
  left: 20px;
  transform: scale(1);
}

@media (width >= 768px) {
  .header__menu {
    top: 39px;
    left: 34px;
  }
}

@media (width >= 1280px) {
  .header__menu {
    left: 55px;
  }
}

.header__menu:hover, .header__menu:focus {
  transform: scale(1.1);
}

.header__logo {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  padding-top: 9px;
  font-family: Montserrat, sans-serif;
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;
  transition: transform .3s;
  display: block;
  transform: scale(1);
}

@media (width >= 768px) {
  .header__logo {
    width: 220px;
    padding-top: 15px;
    font-size: 36px;
    line-height: 36px;
  }
}

.header__logo:hover, .header__logo:focus {
  transform: scale(1.1);
}

.header__description {
  flex-direction: column;
  grid-column: 1 / -1;
  align-items: center;
  margin: 0 20px;
  display: flex;
}

@media (width >= 768px) {
  .header__description {
    order: 1;
    grid-column: 1 / 3;
    align-items: flex-start;
    margin: 0;
    padding-top: 127px;
  }
}

@media (width >= 1280px) {
  .header__description {
    grid-column: 1 / 6;
    margin-top: 206px;
    padding: 0;
  }
}

.header__title {
  text-align: center;
  margin: 20px 0;
}

@media (width >= 768px) {
  .header__title {
    text-align: start;
    margin-top: 0;
    margin-bottom: 38px;
    font-size: 54px;
    line-height: 65px;
  }
}

@media (width >= 1280px) {
  .header__title {
    margin-bottom: 89px;
    font-size: 72px;
    line-height: 85px;
  }
}

.header__button {
  text-transform: uppercase;
  color: #1a5a4c;
  background-color: #fff;
  border: 1px solid #1a5a4c;
  max-width: 280px;
  height: 50px;
  padding: 18px 130px 18px 80px;
  transition: color .3s, border .3s;
  display: inline-block;
  position: relative;
}

.header__button:hover, .header__button:focus {
  color: #f14a27;
  border: 1px solid #f14a27;
}

.header__button:after {
  content: "";
  background-color: #1a5a4c;
  background-image: url("button-arrow.3ead619c.png");
  background-position: center;
  background-repeat: no-repeat;
  width: 50px;
  height: 100%;
  transition: background-color .3s;
  position: absolute;
  top: 0;
  right: 0;
}

.header__button:hover:after, .header__button:focus:after {
  background-color: #f14a27;
}

@media (width >= 768px) {
  .header__button {
    max-width: 270px;
    height: 70px;
  }
}

@media (width >= 1280px) {
  .header__button {
    align-self: end;
  }
}

.header__decoration {
  display: none;
}

@media (width >= 1280px) {
  .header__decoration {
    text-transform: uppercase;
    color: #687480;
    align-items: center;
    font-size: 14px;
    line-height: 14px;
    display: flex;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: rotate(-90deg)translate(-128px);
  }
}

.header__decoration:before {
  content: "";
  background-color: #1a5a4c;
  width: 70px;
  height: 1px;
  margin-right: 20px;
}

.overlay {
  z-index: 998;
  pointer-events: none;
  background-color: #0000;
  width: 100%;
  height: 100vh;
  transition: background-color .3s;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.menu:target ~ .overlay {
  background-color: #00000080;
  overflow: visible;
}

.menu {
  color: #fff;
  pointer-events: none;
  z-index: 999;
  background-color: #1a5a4c;
  width: 100%;
  padding: 27px 20px;
  transition: all .8s;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

@media (width >= 768px) {
  .menu {
    height: 225px;
    padding: 39px 34px;
  }
}

@media (width >= 1280px) {
  .menu {
    height: 233px;
    padding: 40px 122px;
  }
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.menu__close-icon {
  background-image: url("close-icon.28fa339c.png");
  background-position: center;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 27px;
  left: 20px;
}

@media (width >= 768px) {
  .menu__close-icon {
    top: 39px;
    left: 34px;
  }

  .menu__info-fields {
    display: flex;
  }
}

.menu__sections-list {
  border-bottom: 1px solid #ffffff80;
  grid-template-columns: repeat(2, 1fr);
  gap: 28px 24px;
  padding-bottom: 24px;
  padding-left: 72px;
  display: grid;
}

@media (width >= 768px) {
  .menu__sections-list {
    border-bottom: none;
    gap: 20px 24px;
    height: 95px;
    padding-bottom: 0;
    padding-left: 62px;
    padding-right: 38px;
  }
}

@media (width >= 1280px) {
  .menu__sections-list {
    gap: 30px;
    padding-right: 102px;
    font-size: 22px;
    font-weight: 500;
    line-height: 22px;
  }
}

.menu__sections-list--item {
  transition: color .3s;
}

.menu__sections-list--item:hover, .menu__sections-list--item:focus {
  color: #f14a27;
}

.work-schedule {
  padding-top: 24px;
  font-size: 14px;
  line-height: 21px;
}

@media (width >= 768px) {
  .work-schedule {
    border-left: 1px solid #ffffff80;
    padding-top: 0;
    padding-left: 38px;
  }
}

@media (width >= 1280px) {
  .work-schedule {
    padding-left: 102px;
  }
}

.work-schedule__title {
  margin-bottom: 20px;
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 400;
}

@media (width >= 1280px) {
  .work-schedule__title {
    font-size: 14px;
  }
}

@media (width >= 768px) {
  .work-schedule__list {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 26px;
    display: grid;
  }
}

@media (width >= 1280px) {
  .work-schedule__list {
    column-gap: 30px;
    width: 370px;
  }
}

.work-schedule__list--item:nth-child(5) {
  margin-bottom: 16px;
}

.work-schedule .bold-text {
  font-weight: 600;
}

.section {
  padding: 0 20px;
}

@media (width >= 768px) {
  .section {
    padding: 0 34px;
  }
}

@media (width >= 1280px) {
  .section {
    padding: 0 55px;
  }
}

.current-events {
  grid-column: 1 / -1;
  display: grid;
}

.current-events__title {
  text-align: center;
  margin-bottom: 40px;
}

@media (width >= 768px) {
  .current-events__title {
    text-align: start;
    margin-bottom: 48px;
  }
}

@media (width >= 1280px) {
  .current-events__title {
    margin-bottom: 56px;
  }
}

.current-events__list .item {
  margin-bottom: 60px;
}

@media (width >= 768px) {
  .current-events__list .item {
    margin-bottom: 64px;
  }
}

@media (width >= 1280px) {
  .current-events__list .item {
    align-items: center;
    margin-bottom: 104px;
    display: flex;
  }

  .current-events__list .item__link {
    margin-right: 130px;
  }
}

.current-events__list .item__image {
  object-fit: contain;
  object-position: center center;
  grid-column: 1 / -1;
  width: 100%;
  margin-bottom: 20px;
  transition: transform .3s;
  transform: scale(1);
}

.current-events__list .item__image:hover, .current-events__list .item__image:focus {
  transform: scale(1.05);
}

@media (width >= 768px) {
  .current-events__list .item__image {
    margin-bottom: 40px;
  }
}

.current-events__list .item__date-field {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.current-events__list .item__date-field--name {
  text-transform: uppercase;
  color: #687480;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

@media (width >= 768px) {
  .current-events__list .item__date-field--name {
    margin-right: 40px;
    font-size: 14px;
  }
}

.current-events__list .item__date-field--data {
  color: #1a5a4c;
  font-weight: 600;
}

@media (width >= 768px) {
  .current-events__list .item__date-field--data {
    font-size: 14px;
  }

  .current-events__list .item__date-field {
    justify-content: flex-start;
    width: 460px;
    margin-bottom: 32px;
  }
}

.current-events__list .item__title-field {
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
  display: flex;
}

@media (width >= 768px) {
  .current-events__list .item__title-field {
    width: 460px;
  }
}

.current-events__list .item__title {
  color: #0f0e08;
  margin-right: 10px;
  font-size: 28px;
  font-weight: 300;
  line-height: 28px;
  position: relative;
}

@media (width >= 768px) {
  .current-events__list .item__title {
    margin-right: 15px;
    font-size: 36px;
    line-height: 36px;
  }
}

.current-events__list .item__red-square {
  background-color: #f14a27;
  width: 10px;
  height: 10px;
}

.current-events__list .item__description {
  color: #4e4e4e;
  font-weight: 200;
}

@media (width >= 768px) {
  .current-events__list .item__description {
    width: 460px;
  }
}

.romantism {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

@media (width >= 768px) {
  .romantism {
    padding-inline: 34px;
  }
}

.romantism__bg {
  background-image: url("romantism-bg.6195e455.png");
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 210px;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width >= 768px) {
  .romantism__bg {
    width: calc(100% - 64px);
    height: 390px;
    top: 0%;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (width >= 1280px) {
  .romantism__bg {
    width: 100%;
    height: 550px;
  }
}

.romantism__card {
  background-color: #fff;
  width: 280px;
  height: 140px;
  margin-top: 154px;
  margin-bottom: 100px;
  padding-top: 24px;
  padding-bottom: 20px;
  transition: transform .3s;
  transform: scale(1);
  box-shadow: 0 2px #1a5a4c40;
}

.romantism__card:hover, .romantism__card:focus {
  transform: scale(1.05);
}

@media (width >= 768px) {
  .romantism__card {
    width: 570px;
    height: 204px;
    margin-top: 306px;
    margin-bottom: 88px;
    padding: 45px 55px 50px;
  }
}

@media (width >= 1280px) {
  .romantism__card {
    margin-top: 448px;
    margin-bottom: 200px;
  }
}

.romantism__info {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.romantism__info--lecture {
  text-transform: uppercase;
  color: #687480;
  margin-right: 20px;
  font-weight: 500;
}

@media (width >= 768px) {
  .romantism__info--lecture {
    font-size: 14px;
    line-height: 14px;
  }
}

.romantism__info--data {
  color: #1a5a4c;
  font-weight: 700;
}

@media (width >= 768px) {
  .romantism__info--data {
    font-size: 14px;
    line-height: 21px;
  }

  .romantism__info {
    justify-content: space-between;
  }
}

.romantism__title {
  text-align: center;
  color: #0f0e08;
  font-size: 28px;
  font-weight: 400;
  line-height: 28px;
}

@media (width >= 768px) {
  .romantism__title {
    text-align: start;
    font-size: 36px;
    line-height: 36px;
  }
}

.gallery {
  flex-direction: column;
  align-items: center;
  padding-bottom: 100px;
  display: flex;
}

@media (width >= 768px) {
  .gallery {
    align-items: flex-start;
  }
}

@media (width >= 1280px) {
  .gallery {
    padding-bottom: 200px;
  }
}

.gallery__title {
  margin-bottom: 40px;
}

@media (width >= 768px) {
  .gallery__title {
    margin-bottom: 48px;
  }
}

@media (width >= 1280px) {
  .gallery__title {
    margin-bottom: 56px;
    padding-left: 75px;
  }
}

.gallery__container {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.gallery__list {
  justify-content: flex-start;
  margin-bottom: 20px;
  transition: transform .3s;
  display: flex;
}

@media (width >= 1280px) {
  .gallery__list {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.gallery__item {
  margin-right: 20px;
}

@media (width >= 1280px) {
  .gallery__item {
    min-width: auto;
    margin-bottom: 30px;
    margin-right: 30px;
  }

  .gallery__item:nth-child(odd) {
    margin-right: 0;
  }
}

.gallery__image {
  background-size: contain;
  width: 280px;
  height: 410px;
  margin-inline: auto;
  transition: transform .3s;
  display: block;
  transform: scale(1);
}

.gallery__image:hover, .gallery__image:focus {
  transform: scale(1.05);
}

@media (width >= 768px) {
  .gallery__image {
    width: 340px;
    height: 563px;
  }
}

@media (width >= 1280px) {
  .gallery__image {
    margin-inline: 0;
  }

  .gallery__image.image-1 {
    width: 370px;
    height: 600px;
  }

  .gallery__image.image-2 {
    display: none;
  }

  .gallery__image.image-3 {
    width: 770px;
    height: 600px;
  }

  .gallery__image.image-4 {
    width: 770px;
    height: 360px;
  }

  .gallery__image.image-5 {
    width: 370px;
    height: 360px;
  }
}

.gallery__indicators {
  justify-content: center;
  display: flex;
}

@media (width >= 1280px) {
  .gallery__indicators {
    display: none;
  }
}

.gallery__indicator {
  cursor: pointer;
  background-color: #e0e0e0;
  border: none;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  padding: 0;
  transition: background-color .3s;
}

.gallery__indicator:not(:last-child) {
  margin-right: 22px;
}

.gallery__indicator:hover, .gallery__indicator:focus, .gallery__indicator.active {
  background-color: #1a5a4c;
}

.subscription {
  background-image: url("subscription-bg.46e8baf8.png");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  height: 386px;
  padding: 88px 20px;
  display: flex;
}

@media (width >= 768px) {
  .subscription {
    padding: 82px 154px;
  }
}

@media (width >= 1280px) {
  .subscription {
    height: 550px;
    padding: 163px 355px;
  }
}

.subscription__title {
  color: #fff;
  margin-bottom: 20px;
}

@media (width >= 768px) {
  .subscription__title {
    margin-bottom: 16px;
    font-size: 48px;
    line-height: 48px;
  }
}

.subscription__text {
  text-align: center;
  color: #fff;
  margin-bottom: 50px;
  font-size: 18px;
  line-height: 27px;
}

@media (width >= 768px) {
  .subscription__text {
    margin-bottom: 64px;
    line-height: 18px;
  }
}

@media (width >= 1280px) {
  .subscription__text {
    margin-bottom: 72px;
  }
}

.subscription__form {
  justify-content: center;
  width: 100%;
  display: flex;
}

.subscription__input {
  cursor: pointer;
  outline: none;
  width: 100%;
  height: 50px;
  padding: 18px 0 18px 24px;
}

.subscription__input::placeholder {
  font-family: Montserrat, serif;
  font-size: 14px;
  line-height: 14px;
}

@media (width >= 768px) {
  .subscription__input {
    width: 374px;
    height: 70px;
    margin-right: 16px;
  }
}

@media (width >= 1280px) {
  .subscription__input {
    width: 470px;
    margin-right: 30px;
  }
}

.subscription__button {
  background-color: #f14a27;
  background-image: url("subscribe-button-arrow.cc07b0af.png");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  width: 50px;
  height: 50px;
  padding: 0;
  transition: background-color .3s;
}

.subscription__button:hover, .subscription__button:focus {
  background-color: #1a5a4c;
}

@media (width >= 768px) {
  .subscription__button {
    width: 70px;
    height: 70px;
  }
}

.back-to-top-field {
  grid-column: 1 / -1;
  justify-content: flex-end;
  display: grid;
}

.back-to-top-field__button {
  background-image: url("arrow-up-icon.db0be43b.png");
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #1a5a4c;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

@media (width >= 768px) {
  .back-to-top-field {
    display: none;
  }
}

.footer {
  padding: 50px 20px;
}

@media (width >= 768px) {
  .footer {
    padding: 51px 34px;
  }
}

@media (width >= 1280px) {
  .footer {
    padding: 47px 55px 35px;
  }
}

@media (width >= 768px) {
  .footer__info-field {
    justify-content: flex-start;
    align-items: flex-start;
    display: flex;
  }
}

@media (width >= 1280px) {
  .footer__info-field {
    padding-bottom: 87px;
  }
}

.footer__first-section {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

@media (width >= 768px) {
  .footer__first-section {
    grid-template-columns: 1 / -1;
    width: 98px;
    margin-right: 40px;
    display: grid;
  }
}

@media (width >= 1280px) {
  .footer__first-section {
    margin-right: 130px;
  }
}

